import {Injectable} from '@angular/core';
import {Power} from '../hero/power';
import {Powers} from '../data/mock-powers';

@Injectable()

export class PowerService {
    getPowers=function () {
        return Promise.resolve(Powers);
    }
    getPowersSlowly() {
        return new Promise<Power[]>(resolve =>
           setTimeout(() => resolve(Powers), 1000)
        );
    }
    getPowerByFirstOrSecond(firstOrSecond:string) {
        let p:string[]=[];
        return Promise.resolve(Powers).then(
               ()=>{
                     Powers.forEach(element => {
                         console.log(element);
                     });
                     p[0]=Powers[0]["first"];
                     return p[0];
                   }
        );
    }
 }
